.ContextMenuCanary-Level {
    pointer-events: none;
  }

    .ContextMenuCanary-Level.MixPopoverAnimate_animate_entered {
      pointer-events: auto;
    }

    .ContextMenuCanary-Level_playAnimation.MixPopoverAnimate {
      opacity: 0 !important;
      pointer-events: none;
      -webkit-transition: opacity 0.2s, -webkit-transform 0.2s !important;
      transition: opacity 0.2s, -webkit-transform 0.2s !important;
      transition: opacity 0.2s, transform 0.2s !important;
      transition: opacity 0.2s, transform 0.2s, -webkit-transform 0.2s !important;
      -webkit-transform: var(--mix-dropdown-animate-transform) !important;
              transform: var(--mix-dropdown-animate-transform) !important;
    }
