.SelectLoader {
  --loader-height: 0;
  width: 100%;
  height: 100%;
  cursor: wait;
}

  .SelectLoader_size_xs {
      --loader-height: calc(var(--control-space-xs) + var(--control-text-size-xs));
    }

  .SelectLoader_size_s {
      --loader-height: calc(var(--control-space-s) + var(--control-text-size-s));
    }

  .SelectLoader_size_m {
      --loader-height: calc(var(--control-space-m) + var(--control-text-size-m));
    }

  .SelectLoader_size_l {
      --loader-height: calc(var(--control-space-l) + var(--control-text-size-l));
    }

  .SelectLoader-Loader {
    --backgroundColor: var(--color-control-typo-disable);
    z-index: 1;
  }

  .SelectLoader_mode_empty {
      height: var(--loader-height);
    }

  .SelectLoader_mode_empty .SelectLoader-Loader {
        left: calc(var(--space-s) + 10px);
      }

  .SelectLoader_mode_blur {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
    }

  .SelectLoader_mode_blur::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: var(--color-bg-default);
        opacity: 0.7;
      }
