.ContextMenuLevelCanary {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 240px;
  padding: var(--space-xs) 0;
  background: var(--color-bg-default);
  border: var(--control-border-width) solid var(--color-control-bg-border-default);
  border-radius: var(--control-radius);
  -webkit-box-shadow: var(--shadow-layer);
          box-shadow: var(--shadow-layer);
}

  .ContextMenuLevelCanary:not(.ContextMenuLevelCanary_firstLevel).ContextMenuLevelCanary_direction_rightStartUp,
    .ContextMenuLevelCanary:not(.ContextMenuLevelCanary_firstLevel).ContextMenuLevelCanary_direction_leftStartUp {
      -webkit-transform: translateY(calc(-1 * (var(--space-xs) + 1px)));
              transform: translateY(calc(-1 * (var(--space-xs) + 1px)));
    }

  .ContextMenuLevelCanary:not(.ContextMenuLevelCanary_firstLevel).ContextMenuLevelCanary_direction_rightStartDown,
    .ContextMenuLevelCanary:not(.ContextMenuLevelCanary_firstLevel).ContextMenuLevelCanary_direction_leftStartDown {
      -webkit-transform: translateY(calc(var(--space-xs) + 1px));
              transform: translateY(calc(var(--space-xs) + 1px));
    }

  .ContextMenuLevelCanary-Divider {
    --vertical-space: 0;
    height: var(--control-border-width);
    margin: var(--vertical-space) var(--space-s);
    background: var(--color-control-bg-border-default);
  }

  .ContextMenuLevelCanary-Divider_size_l {
        --vertical-space: var(--space-xs);
      }

  .ContextMenuLevelCanary-Divider_size_m,
      .ContextMenuLevelCanary-Divider_size_s,
      .ContextMenuLevelCanary-Divider_size_xs {
        --vertical-space: var(--space-2xs);
      }

  .ContextMenuLevelCanary-Group_size_xs > .ContextMenuLevel-Item:not(:last-child) {
      margin-bottom: var(--space-2xs);
    }

  .ContextMenuLevelCanary-Header {
    --top-space: 0;
    --bottom-space: 0;
    margin: 0 var(--space-s);
    padding: var(--top-space) 0 var(--bottom-space) 0;
    border-bottom: solid var(--control-border-width) var(--color-control-bg-border-default);
    letter-spacing: 0.05em;
  }

  .ContextMenuLevelCanary-Header_size_xs,
      .ContextMenuLevelCanary-Header_size_s {
        --top-space: var(--space-xs);
        --bottom-space: var(--space-2xs);
      }

  .ContextMenuLevelCanary-Header_size_m {
        --top-space: var(--space-s);
        --bottom-space: var(--space-xs);
      }

  .ContextMenuLevelCanary-Header_size_l {
        --top-space: var(--space-s);
        --bottom-space: var(--space-s);
      }

  .ContextMenuLevelCanary-Header_first {
      padding: calc(var(--top-space) - var(--space-xs)) 0 var(--bottom-space) 0;
    }
